@import "_variables.scss";

* {
    box-sizing: border-box;
}

html {
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100vh;
}

body {
    margin: 0px;
    padding: 0px;
    width: 100%;
    height: 100%;
}


@supports (display:grid) {
    .uoc-container {
        display: grid;
        //  grid-template-columns: 5% 9% 9% 9% 5% 9% 9% 9% 5% 9% 9% 1%;
        grid-template-rows: auto;
        grid-template-columns: auto;
        grid-template-areas:
            " c1 c1 c1 . . . . . . . . ."
            " . c2 c2 c2 . c3 c3 c3 . c4 c4 c4"
            " .  .  c5 c5 c5 c5 .  c6 c6 c6 c6 . "
            " c7 c7 c7 c7 c7 c7 c7 c7 c7 c7 c7 c7";
        background-image: url("../images/fotoMusicaCC.jpg");
        background-size: cover;
        row-gap: 3em;
        padding-top: 1em;
        padding-bottom: 1em;
        min-height: auto;
        //   overflow: auto;

    }

    .caja1 {
        grid-area: c1;
        background-color: yellow;

    }

    .caja2 {
        grid-area: c2;
        background-color: red;
    }

    .caja3 {
        grid-area: c3;
        background-color: blue;
    }

    .caja4 {
        grid-area: c4;
        background-color: purple;
    }

    .caja5 {
        grid-area: c5;
        background-color: pink;
    }

    .caja6 {
        grid-area: c6;
        background-color: black;

    }

    .caja7 {
        grid-area: c7;
        background-color: lightcyan;
    }


    // CON SOPORTE GRID Y PANTALLA PEQUEÃ‘A 
    @media (max-width:576px) {
        .uoc-container {
            grid-template-areas:
                " . c1 c1 c1 c1 c1 c1 c1 c1 c1 c1 ."
                " . c2 c2 c2 c2 c2 c2 c2 c2 c2 c2 ."
                " . c3 c3 c3 c3 c3 c3 c3 c3 c3 c3 ."
                " . c4 c4 c4 c4 c4 c4 c4 c4 c4 c4 ."
                " . c5 c5 c5 c5 c5 c5 c5 c5 c5 c5 ."
                " . c6 c6 c6 c6 c6 c6 c6 c6 c6 c6 ."
                " . c7 c7 c7 c7 c7 c7 c7 c7 c7 c7 .";
            row-gap: 3em;
            padding-top: 1em;

        }
    }


    @media (orientation: landscape) {
        .uoc-container {
            min-height: 230vh;
        }

    }
}


/*-----------------------------------------------------------------------------------------*/
/* SIN SOPORTE GRID */
/*-----------------------------------------------------------------------------------------*/

@supports (display:grid) {
    .uoc-container {
        display: block;
        margin: 0px;
        padding: 1em;
        width: 100%;
        height: 100%;
        background-image: url("../images/fotoMusicaCC.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-color: blue;
    }


    .caja1 {
        background-color: yellow;
        margin-left: 5em;
        width: 20%;
        clear: both;

    }

    .caja2 {
        background-color: red;
        margin-top: 2em;
        margin-left: 10%;
        width: 20%;
        float: left;
    }

    .caja3 {
        background-color: blue;
        margin-top: 2em;
        margin-left: 10%;
        width: 20%;
        float: left;
    }

    .caja4 {
        background-color: purple;
        margin-top: 2em;
        margin-left: 10%;
        width: 20%;
        float: left;

    }

    .caja5 {
        background-color: pink;
        margin-top: 2em;
        margin-left: 20%;
        width: 25%;
        float: left;
        clear: left;
    }

    .caja6 {
        background-color: black;
        margin-top: 2em;
        margin-left: 10%;
        width: 25%;
        float: left;
    }

    .caja7 {
        height: auto;
        background-color: lightcyan;
        margin-top: 2em;
        margin-left: 5%;
        width: 85%;
        float: left;
    }

    @media (max-width:576px) {
        .uoc-container {
            height: auto;
            overflow: auto;
            background-color: red;
        }

        .caja1,
        .caja2,
        .caja3,
        .caja4,
        .caja5,
        .caja6,
        .caja7 {
            float: none;
            width: 96%;
            padding: 0px;
            margin-left: 2%;
            margin-right: 2%;
        }

    }
}